import { useEffect, useState } from "react"
import { createPost } from "../api"


const CreatePost = (props) => {
    const loadedPosts = props.loadedPosts
    const setLoadedPosts = props.setLoadedPosts
    const setPostsToDisplay = props.setPostsToDisplay
    const [description, setDescription] = useState("")
    const [location, setLocation] = useState("")
    const [price, setPrice] = useState("")
    const [title, setTitle] = useState("")
    const [willDeliver, setWillDeliver] = useState(false)

    const handleSubmitNewPost = async (event) =>{
        event.preventDefault()
    let postInfo = {
        post:{
        title: `${title}` ,
        description: `${description}`,
        price: `${price}`,
        location: `${location}`,
        willDeliver: `${willDeliver}`
        }
    }
   
  const data = await createPost(postInfo)
  console.log(data)
    setPostsToDisplay([data.data.post,...loadedPosts])
    setLoadedPosts([data.data.post,...loadedPosts])
    }

    const handleDescriptionChange = (event)=>{
        setDescription(event.target.value)
    }
    const handleLocationChange = (event)=>{
        setLocation(event.target.value)
    }
    const handlePriceChange = (event)=>{
        setPrice(event.target.value)
    }
    const handleTitleChange = (event)=>{
        setTitle(event.target.value)
    }
    const handleWillDeliverChange = (event)=>{
        setWillDeliver(event.target.checked)
    }



    return (
        <aside id = "createpost">
                <h1>Create Post</h1>
                <form onSubmit={handleSubmitNewPost}>
                    <label htmlFor="description">Description:</label>
                    <input type="text" name="description" value = {description} onChange={handleDescriptionChange}/>
                     <label htmlFor="location">Location:</label>
                     <input type="text" name="location" value = {location} onChange={handleLocationChange}/>
                     <label htmlFor="price">Price:</label>
                     <input type="text" name="price" value = {price} onChange={handlePriceChange}/>
                     <label htmlFor="title">Title:</label>
                     <input type="text" name="title" value = {title} onChange={handleTitleChange}/>
                     <label htmlFor="willdeliver">Will Deliver</label>
                     <input type="checkbox" name="willdeliver" value = {willDeliver} onChange={handleWillDeliverChange}/>
                     <button type="submit">Submit Post</button>
                </form>
        </aside>
    )
}

export default CreatePost